* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width: thin;
}

.main {
  height: 100%;
  width: 100%;
  background-color: rgb(21, 21, 21);
}

.Auth-form-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 10px;
  margin-right: 5%;
}

.Auth-form {
  min-width: 28em;
  /* height: 35em; */
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}
.mainbox {
    background-color: #95c2de;
    margin: auto;
    height: 400px;
    width: 600px;
    position: relative;
}

.err {
    color: #ffffff;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 11rem;
    position: absolute;
    left: 20%;
    top: 8%;
}

.far {
    color: #ffffff;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 9.5rem;
    position: absolute;
    left: 40%;
    top: 8%;
}

.err2 {
    color: #ffffff;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 11rem;
    position: absolute;
    left: 68%;
    top: 8%;
}

.msg {
    text-align: center;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.6rem;
    position: absolute;
    left: 16%;
    top: 45%;
    width: 75%;
    margin-top: 20%;
}

link {
    text-decoration: none;
    color: white;
}

link:hover {
    text-decoration: underline;
}
